.list-container {
  position: relative;
  padding-bottom: 30px;
  background-color: white;
}

.list-container::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 10px;
  background-color: #eee;
  z-index: 2;
}

.button-wrapper {
  position: relative;
  z-index: 3;
}

.load-more {
  position: absolute;
  bottom: 40px;
  left: 0;
  width: 100%;
  padding: 10px;
  color: black;
  border-style: none;
  background-color: white;
  z-index: 3;
}

.load-more button {
  border: none;
  background-color: #eee;
  padding: 10px 20px;
  cursor: pointer;
}

.load-more button:hover {
  background-color: #ddd;
}
